@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body{background-color:black}.app{text-align:center;font-family:'Lato', sans-serif}div#searchbox-wrapper{display:flex;justify-content:center;height:100vh}div#searchbox-wrapper #searchbox{align-self:center;width:450px;padding:15px;border-radius:5px;max-height:450px;color:white}div#searchbox-wrapper #searchbox div.input-wrapper{position:relative}div#searchbox-wrapper #searchbox div.input-wrapper input#query[type=text]{font-size:19px;border-radius:5px;box-sizing:border-box;width:100%;border:none;outline:none;-webkit-transition:0.4s;transition:0.4s;padding:18px 20px;background:#1d1d1d;text-align:left;color:white}div#searchbox-wrapper #searchbox div.input-wrapper input#query[type=text] ~ .focus-border:before{content:"";position:absolute;top:0;right:0;width:0;height:2px;background-color:red;-webkit-transition:0.2s;transition:0.2s;-webkit-transition-delay:0.2s;transition-delay:0.2s}div#searchbox-wrapper #searchbox div.input-wrapper input#query[type=text] ~ .focus-border:after{content:"";position:absolute;top:0;right:0;width:0;height:2px;background-color:red;-webkit-transition:0.2s;transition:0.2s;-webkit-transition-delay:0.2s;transition-delay:0.2s;top:auto;bottom:0;right:auto;left:0;-webkit-transition-delay:0.6s;transition-delay:0.6s}div#searchbox-wrapper #searchbox div.input-wrapper input#query[type=text] ~ .focus-border i:before{content:"";position:absolute;top:0;left:0;width:2px;height:0;background-color:red;-webkit-transition:0.2s;transition:0.2s}div#searchbox-wrapper #searchbox div.input-wrapper input#query[type=text] ~ .focus-border i:after{content:"";position:absolute;top:0;left:0;width:2px;height:0;background-color:red;-webkit-transition:0.2s;transition:0.2s;left:auto;right:0;top:auto;bottom:0;-webkit-transition-delay:0.4s;transition-delay:0.4s}div#searchbox-wrapper #searchbox div.input-wrapper input#query[type=text]:focus ~ .focus-border:before{width:100%;-webkit-transition:0.2s;transition:0.2s;-webkit-transition-delay:0.6s;transition-delay:0.6s}div#searchbox-wrapper #searchbox div.input-wrapper input#query[type=text]:focus ~ .focus-border:after{width:100%;-webkit-transition:0.2s;transition:0.2s;-webkit-transition-delay:0.6s;transition-delay:0.6s;-webkit-transition-delay:0.2s;transition-delay:0.2s}div#searchbox-wrapper #searchbox div.input-wrapper input#query[type=text]:focus ~ .focus-border i:before{height:100%;-webkit-transition:0.2s;transition:0.2s}div#searchbox-wrapper #searchbox div.input-wrapper input#query[type=text]:focus ~ .focus-border i:after{height:100%;-webkit-transition:0.2s;transition:0.2s;-webkit-transition-delay:0.4s;transition-delay:0.4s}div#searchbox-wrapper #searchbox div.suggestionsBox{margin-top:20px;position:relative;background-color:#1d1d1d;border-radius:10px;border-color:red;border-style:solid;border-width:2px;-webkit-transition:ease-in-out .5s;transition:ease-in-out .5s;padding:10px 20px}div#searchbox-wrapper #searchbox div.suggestionsBox:before{content:"";border-color:transparent transparent red transparent;outline:none;border-style:solid;border-width:0px 10px 10px 10px;height:0px;width:0px;position:absolute;top:-10px}div#searchbox-wrapper #searchbox div.suggestionsBox ul{margin:0 auto;padding:0 20px;max-height:450px;overflow-y:auto;border:1px solid rgba(0,0,0,0.1);padding:5px 5px 0 5px;border-left:none;border-right:none;list-style-type:none}div#searchbox-wrapper #searchbox div.suggestionsBox ul:-webkit-scrollbar{width:6px;background-color:#F5F5F5}div#searchbox-wrapper #searchbox div.suggestionsBox ul li{text-transform:uppercase;font-weight:bold;font-family:'Lato', sans-serif;letter-spacing:0.1em;text-align:center;line-height:2em;position:relative;overflow:hidden;z-index:1;-webkit-transition:0.5s;transition:0.5s;padding:10px 15px 10px 25px;margin:1em 0 1em 0;border:0.3em solid goldenrod;width:100%;color:goldenrod;box-sizing:border-box;cursor:pointer;border-radius:0.5em}div#searchbox-wrapper #searchbox div.suggestionsBox ul li:hover{color:black}div#searchbox-wrapper #searchbox div.suggestionsBox ul li:hover span{-webkit-transform:translateY(0) scale(2);transform:translateY(0) scale(2)}div#searchbox-wrapper #searchbox div.suggestionsBox ul li span{position:absolute;width:25%;height:100%;background-color:goldenrod;-webkit-transform:translateY(150%);transform:translateY(150%);border:none;border-top:none;border-radius:50%;-webkit-transition:0.5s;transition:0.5s;z-index:-1;box-shadow:none}div#searchbox-wrapper #searchbox div.suggestionsBox ul li span:nth-child(1){left:calc((0)*25%);-webkit-transition-delay:calc((0)*0.1s);transition-delay:calc((0)*0.1s)}div#searchbox-wrapper #searchbox div.suggestionsBox ul li span:nth-child(2){left:calc((1)*25%);-webkit-transition-delay:calc((1)*0.1s);transition-delay:calc((1)*0.1s)}div#searchbox-wrapper #searchbox div.suggestionsBox ul li span:nth-child(3){left:calc((2)*25%);-webkit-transition-delay:calc((2)*0.1s);transition-delay:calc((2)*0.1s)}div#searchbox-wrapper #searchbox div.suggestionsBox ul li span:nth-child(4){left:calc((3)*25%);-webkit-transition-delay:calc((3)*0.1s);transition-delay:calc((3)*0.1s)}

